header {
  background-color: #6aa1bc;
  justify-content: center;
  padding: 15px 0;
  display: flex;
}

.search-form {
  justify-content: center;
  width: 50%;
  display: flex;
}

.search-form button:hover {
  border-width: 5px;
  border-color: #000;
}

.search-form button {
  cursor: pointer;
  border: none;
}

.search-form input {
  border: none;
  width: 75%;
  padding: 5px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  padding: 10px;
  display: flex;
}

.photo-card {
  border: 1px solid #000;
  width: 30%;
}

.photo-card img {
  max-width: 100%;
}

.photo-wrapper {
  height: 80%;
}

.photo-wrapper img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.info {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 2px;
  display: flex;
}

.info p {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.loader-back {
  background: #00000043;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  color: #fff;
  width: 6px;
  height: 40px;
  animation: .75s ease-out infinite paddles;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before {
  content: "";
  background-color: #de3500;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  margin: 0 auto;
  animation: .6s ease-out infinite ballbounce;
  position: absolute;
  top: 15px;
  left: 0;
  right: 0;
}

@keyframes paddles {
  0% {
    box-shadow: -25px -10px, 25px 10px;
  }

  50% {
    box-shadow: -25px 8px, 25px -10px;
  }

  100% {
    box-shadow: -25px -10px, 25px 10px;
  }
}

@keyframes ballbounce {
  0% {
    transform: translateX(-20px)scale(1, 1.2);
  }

  25% {
    transform: scale(1.2, 1);
  }

  50% {
    transform: translateX(15px)scale(1, 1.2);
  }

  75% {
    transform: scale(1.2, 1);
  }

  100% {
    transform: translateX(-20px);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

h1 {
  margin-bottom: 5px;
  font-size: 1em;
}

img {
  display: block;
}

p {
  font-size: .9em;
}

.hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  top: -9999px;
  left: -9999px;
}

.fast-hide {
  display: none;
}

